.content .card{
    height: calc(100vh - 391px) !important;
    max-height: calc(100vh - 318px) !important;
    overflow: auto;
    border-radius: 9px;
    box-shadow: 0 2px 2px #ddd !important;
}

.stats{
    .card{
        height: 153px!important;
        border-radius: 9px;
        box-shadow: 0 2px 2px #ddd !important;
    }

    &.list{
        height: calc(100vh) !important;
        max-height: calc(100vh) !important;
        overflow: auto;
       


        .card{
            height: 227px !important;
            overflow: hidden;
            box-shadow: 4px 4px 15px -3px #3d3d3d!important;

            hr{
                border-top: 1px solid #cfcfd0;
            }
        }
    }
}

.vid{
    height: calc(100vh) !important;
}





///@at-root
.dropdown-toggle:after{
    position: relative;
    top: -9px;
    left: 4px;
}

.custom-select{
    background: #e9ecef url(../images/caret.svg) no-repeat right 0.75rem center;
    background-size: 11px;
    border: 0;
    border-radius: 0;
    display: inline-block;
    width: 200px;
    height: calc(2.25rem + 7px);
    padding: .375rem 1.75rem .375rem .75rem;
    line-height: 1.5;
    color: #495057;
    vertical-align: middle;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;

    &:focus {
        outline: 0;
        box-shadow: none;
        border: 0;
    }
}


#fountainG{
	position:relative;
	width:234px;
	height:28px;
	margin:auto;
}

.fountainG{
	position:absolute;
	top:0;
	background-color:#7c01e8;
	width:28px;
	height:28px;
	animation-name:bounce_fountainG;
		-o-animation-name:bounce_fountainG;
		-ms-animation-name:bounce_fountainG;
		-webkit-animation-name:bounce_fountainG;
		-moz-animation-name:bounce_fountainG;
	animation-duration:1.5s;
		-o-animation-duration:1.5s;
		-ms-animation-duration:1.5s;
		-webkit-animation-duration:1.5s;
		-moz-animation-duration:1.5s;
	animation-iteration-count:infinite;
		-o-animation-iteration-count:infinite;
		-ms-animation-iteration-count:infinite;
		-webkit-animation-iteration-count:infinite;
		-moz-animation-iteration-count:infinite;
	animation-direction:normal;
		-o-animation-direction:normal;
		-ms-animation-direction:normal;
		-webkit-animation-direction:normal;
		-moz-animation-direction:normal;
	transform:scale(.3);
		-o-transform:scale(.3);
		-ms-transform:scale(.3);
		-webkit-transform:scale(.3);
		-moz-transform:scale(.3);
	border-radius:19px;
		-o-border-radius:19px;
		-ms-border-radius:19px;
		-webkit-border-radius:19px;
		-moz-border-radius:19px;
}

